import { useEffect, useState } from "react";
import useKeyPressed from "./useKeyPressed";

function useFormValues(selectedChar: string, isCursorOverKeyboard: boolean) {
  const isSpaceKeyPressed = useKeyPressed("Space");
  const isTabKeyPressed = useKeyPressed("Tab");

  const [values, setValues] = useState(["", "", "", ""]);
  const [index, setIndex] = useState(0);

  useEffect(() => {
    if (isTabKeyPressed) {
      const newIndex = index === values.length - 1 ? 0 : index + 1;

      setIndex(newIndex);
    }
  }, [isTabKeyPressed]);

  useEffect(() => {
    if (isSpaceKeyPressed && isCursorOverKeyboard) {
      const newValues = [...values];
      newValues[index] = newValues[index] + selectedChar;
      setValues(newValues);
    }
  }, [isSpaceKeyPressed, isCursorOverKeyboard]);

  return values;
}

export default useFormValues;
