import useCharSelected from "./useCharSelected";
import useCursorOverKeyboard from "./useCursorOverKeyboard";
import useCursorPosition from "./useCursorPosition";
import useCursorRotation from "./useCursorRotation";
import useFormValues from "./useFormValues";
import useKeyboardPosition from "./useKeyboardPosition";
import useKeyboardRotation from "./useKeyboardRotation";

function useMainState() {
  const cursorPosition = useCursorPosition();
  const keyboardPosition = useKeyboardPosition();
  const cursorRotation = useCursorRotation();
  const keyboardRotation = useKeyboardRotation();
  const isCursorOverKeyboard = useCursorOverKeyboard(
    cursorPosition,
    keyboardPosition
  );
  const selectedChar = useCharSelected(cursorRotation, keyboardRotation);
  const formValues = useFormValues(selectedChar, isCursorOverKeyboard);

  return {
    cursorPosition,
    keyboardPosition,
    cursorRotation,
    keyboardRotation,
    isCursorOverKeyboard,
    selectedChar,
    formValues,
  };
}

export default useMainState;
