import cs from "classnames";
import React from "react";
import PHRASE from "../../phrase";
import styles from "./Keyboard.module.scss";

interface IProps {
  position: number[];
  rotation: number;
  isCursorOverKeyboard: boolean;
}

function Keyboard({ position, rotation, isCursorOverKeyboard }: IProps) {
  return (
    <div
      style={{
        transform: `rotate(${rotation + 88}deg)`,
        top: `${position[1]}px`,
        left: `${position[0]}px`,
      }}
      className={cs(styles.Keyboard, { [styles.Green]: isCursorOverKeyboard })}
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        viewBox="0 0 500 500"
      >
        <path
          d="M50,250c0-110.5,89.5-200,200-200s200,89.5,200,200s-89.5,200-200,200S50,360.5,50,250"
          id="textcircle"
          fill="none"
        ></path>
        <text textLength="1250">
          <textPath xlinkHref="#textcircle" fill="currentColor">
            {PHRASE}
          </textPath>
        </text>
      </svg>
      <div className={styles.center}></div>
    </div>
  );
}

export default Keyboard;
