import { useEffect, useState } from "react";
import PHRASE from "../phrase";

const phrase = PHRASE.replace(/-/g, "");

function useCharSelected(cursorRotation: number, keyboardRotation: number) {
  const [char, setChar] = useState("");

  useEffect(() => {
    let degrees = cursorRotation + keyboardRotation;
    degrees = degrees >= 360 || degrees <= -360 ? degrees % 360 : degrees;

    let chunkLength = 360 / phrase.length;
    let charIndex = Math.trunc(degrees / chunkLength);

    setChar(phrase.at(charIndex) ?? "");
  }, [cursorRotation, keyboardRotation]);

  return char;
}

export default useCharSelected;
