import { useEffect, useState } from "react";

function useKeyboardRotation() {
  const [deg, setDeg] = useState(0);

  useEffect(() => {
    const id = setTimeout(() => {
      setDeg((v) => v + 20);
    }, 1000);

    return () => {
      clearTimeout(id);
    };
  }, [deg]);

  return deg;
}

export default useKeyboardRotation;
