import React from "react";
import styles from "./Cursor.module.scss";

interface IProps {
  position: number[];
  rotation: number;
  char: string | null;
  isCursorOverKeyboard: boolean;
}

function Cursor({ position, rotation, char, isCursorOverKeyboard }: IProps) {
  return (
    <div
      style={{
        transform: `rotate(${rotation}deg)`,
        top: `${position[1]}px`,
        left: `${position[0]}px`,
      }}
      className={styles.Cursor}
    >
      {isCursorOverKeyboard && char}
    </div>
  );
}

export default Cursor;
