import { useEffect, useState } from "react";

function useCursorOverKeyboard(cPosition: number[], kPosition: number[]) {
  const [isCursorOverKeyboard, setIsCursorOverKeyboard] = useState(false);

  useEffect(() => {
    const kCenterPosition = [kPosition[0] + 150, kPosition[1] + 150];

    const isOver =
      Math.abs(cPosition[0] + 25 - kCenterPosition[0]) < 25 &&
      Math.abs(cPosition[1] + 25 - kCenterPosition[1]) < 25;

    setIsCursorOverKeyboard(isOver);
  }, [cPosition, kPosition]);

  return isCursorOverKeyboard;
}

export default useCursorOverKeyboard;
