import { useEffect, useState } from "react";
import useKeyPressed from "./useKeyPressed";

function useCursorRotation() {
  const isShiftLeftPressed = useKeyPressed("ShiftLeft");
  const isShiftRightPressed = useKeyPressed("ShiftRight");
  const [deg, setDeg] = useState(0);

  useEffect(() => {
    if (isShiftLeftPressed) {
      setDeg(deg + 10);
    }

    if (isShiftRightPressed) {
      setDeg(deg - 10);
    }
  }, [isShiftLeftPressed, isShiftRightPressed]);

  return deg;
}

export default useCursorRotation;
