import { useEffect, useState } from "react";

const useCursorPosition = () => {
  const [position, setPosition] = useState([300, 1500]);

  useEffect(() => {
    const id = setTimeout(() => {
      setPosition(([x, y]) => [x + 2, y + 5]);
    }, 1000);

    return () => {
      clearTimeout(id);
    };
  }, [position]);

  return position;
};

export default useCursorPosition;
